"use client";

import { useState } from "react";
import axios from "axios";
import { toast } from "sonner";
import { Button } from "@/components/ui/button";
import Spinner from "@/components/ui/spinner";

const API_BASE_URL = "http://127.0.0.1:8000/api/qa/query";

export default function AskQuestionBox() {
  const [documentId, setDocumentId] = useState("");
  const [question, setQuestion] = useState("");
  const [response, setResponse] = useState<any>(null);
  const [loading, setLoading] = useState(false);

  const handleAsk = async () => {
    if (!documentId || !question) {
      toast.error("Please provide both Document ID and Question.");
      return;
    }

    try {
      setLoading(true);
      const res = await axios.post(
        `${API_BASE_URL}?document_id=${documentId}&question=${encodeURIComponent(question)}`
      );
      setResponse(res.data);
      toast.success("‚úÖ Question answered successfully");
    } catch (err: any) {
      toast.error(`Error: ${err.response?.data?.detail || "Failed to get answer"}`);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div>
      <h2 className="text-2xl font-semibold text-white mb-4">‚ùì Ask a Question</h2>
      <div className="space-y-3">
        <input
          type="text"
          placeholder="Enter Document ID"
          value={documentId}
          onChange={(e) => setDocumentId(e.target.value)}
          className="w-full bg-gray-800 text-gray-100 border border-gray-700 rounded-lg p-2 focus:ring-2 focus:ring-indigo-600 outline-none"
        />
        <textarea
          placeholder="Type your question here..."
          value={question}
          onChange={(e) => setQuestion(e.target.value)}
          className="w-full bg-gray-800 text-gray-100 border border-gray-700 rounded-lg p-2 h-24 focus:ring-2 focus:ring-indigo-600 outline-none"
        />
        <Button onClick={handleAsk} disabled={loading}>
          {loading ? <Spinner /> : "Ask Question"}
        </Button>
      </div>

      {response && (
        <div className="mt-6 bg-gray-800/60 p-4 rounded-xl border border-gray-700 text-gray-200">
          <h3 className="text-lg font-semibold text-indigo-400 mb-2">Answer:</h3>
          <p className="text-gray-300 mb-4 whitespace-pre-line">{response.answer}</p>

          <h4 className="font-medium text-indigo-300 mb-1">Sources:</h4>
          <ul className="list-disc list-inside text-gray-400 space-y-1">
            {response.sources?.map((src: any, i: number) => (
              <li key={i}>
                <span className="text-gray-300">{src.chunk_text}</span> ‚Äî{" "}
                <span className="text-indigo-400">
                  Score: {src.relevance_score.toFixed(2)}
                </span>
              </li>
            ))}
          </ul>

          <p className="text-sm text-gray-500 mt-3">
            Processing time: {response.processing_time_seconds}s
          </p>
        </div>
      )}
    </div>
  );
}