"use client";

import { useState } from "react";
import axios from "axios";
import { toast } from "sonner";
import { Button } from "@/components/ui/button";
import Spinner from "@/components/ui/spinner";

const API_BASE_URL = "http://127.0.0.1:8000/api/documents/upload";

export default function UploadBox({ onUpload }: { onUpload: () => void }) {
  const [file, setFile] = useState<File | null>(null);
  const [loading, setLoading] = useState(false);

  const handleUpload = async () => {
    if (!file) {
      toast.error("Please select a file first!");
      return;
    }

    const formData = new FormData();
    formData.append("file", file);

    try {
      setLoading(true);
      const res = await axios.post(API_BASE_URL, formData, {
        headers: { "Content-Type": "multipart/form-data" },
      });
      toast.success(`‚úÖ Uploaded: ${res.data.filename}`);
      onUpload();
    } catch (err: any) {
      toast.error(`‚ùå Upload failed: ${err.response?.data?.detail || err.message}`);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="text-center space-y-4">
      <h2 className="text-2xl font-semibold text-white mb-2">üìÅ Upload Document</h2>
      <p className="text-gray-400 mb-4">Upload your PDF or TXT files to process and store them.</p>

      <div className="flex flex-col items-center gap-3">
        <input
          type="file"
          accept=".pdf,.txt"
          onChange={(e) => setFile(e.target.files?.[0] || null)}
          className="block w-full text-gray-300 bg-gray-800 rounded-lg border border-gray-700 cursor-pointer focus:outline-none p-2"
        />
        <Button onClick={handleUpload} disabled={loading}>
          {loading ? <Spinner /> : "Upload File"}
        </Button>
      </div>
    </div>
  );
}
